/* eslint-disable no-return-assign */
import React from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import config from '../../data/SiteConfig'
import MainHeader from '../components/Layout/Header'

const BodyContainer = styled.div`
  padding: ${props => props.theme.sitePadding};
`
const IndexHeadContainer = styled.div`
  background: ${props => props.theme.brand};
  padding: ${props => props.theme.sitePadding};
  text-align: center;
`
const Hero = styled.div`
  padding: 50px 0;
  & > h1 {
    font-weight: 600;
  }
`

class Download extends React.Component {

  componentDidMount() {
    if (typeof window !== `undefined`) {

      const typeformEmbed = require('@typeform/embed')

      typeformEmbed.makeWidget(this.form, "https://opticdev.typeform.com/to/gwJF5R", {
        hideFooter: true,
        hideHeaders: true
      });
    }
  }

  render() {
    return (
      <div className="index-container">
        <Helmet title={'Download Optic'} />
        <main style={{
          height: '100%',
          position: 'absolute',
          width: '100%'
        }}>
          <MainHeader
            siteTitle={config.siteTitle}
            siteDescription={config.siteDescription}
            location={this.props.location}
            logo={config.siteLogo}
          />
          <IndexHeadContainer>
            <Hero>
              <div style={{textAlign: 'center' }}>
                <a style={{fontSize: '30px', color: '#FFF' }} href={"https://s3.us-east-2.amazonaws.com/opticreleases/Optic1.0.4.zip"}>&darr; <span style={{textDecoration: 'underline'}}>Click to Download Optic</span></a>
              </div>
            </Hero>
          </IndexHeadContainer>
          <BodyContainer style={{height: '90%'}}>
            <div
              ref={(form) => this.form = form}
              style={{width: '100%', height: '100%'}}
            />
          </BodyContainer>
        </main>
      </div>
    )
  }
}

export default Download



